<template>
  <div>
    <nav class="navbar navbar-expand-sm navbar-light bg-toolbar pt-1 pb-1">
      <span class="brand">Cnsource</span>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarText">
        <ul class="navbar-nav mr-auto">
          <li :class="{'nav-link':true,'active':isActive==='home'}" @click="toolbarClick('home')">
            <a class="nav-link" href="#/content">首页</a>
          </li>
          <li :class="{'nav-link':true,'active':isActive==='features'}" @click="toolbarClick('features')">
            <a class="nav-link" href="#/admin">管理</a>
          </li>
          <li :class="{'nav-link':true,'active':isActive==='pricing'}" @click="toolbarClick('pricing')">
            <a class="nav-link" href="#/edit">写稿子</a>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: "ToolBar",
  data(){
    return{
      isActive:"home"
    }
  },
  methods:{
    toolbarClick(value){
      this.isActive = value
    }
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@700&display=swap');
.navbar-nav{
  font-size: 1.2em;
  font-weight: 500;
}
.bg-toolbar{
  background-color: #f7fff7;
}
.brand{
  color: #ff6b6b;
  font-size: 2em;
  font-family: 'Cairo', sans-serif;
}
</style>



