<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-sm-12 col-md-12 col-lg-9">
        <form>
          <div class="form-group">
            <label for="exampleInputEmail1"><b>图标名称</b></label>
            <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" v-model="Link.name" placeholder="eg:CSDN / Github">
          </div>
          <div class="btn-group mt-2 mb-2" role="group" aria-label="Basic example">
            <button type="button" :class="['btn',isActive=='自媒体'?'btn-success':'btn-outline-secondary']" @click="choseType('自媒体')">自媒体</button>
            <button type="button" :class="['btn',isActive=='博客'?'btn-success':'btn-outline-secondary']" @click="choseType('博客')">博客</button>
            <button type="button" :class="['btn',isActive=='联系方式'?'btn-success':'btn-outline-secondary']" @click="choseType('联系方式')">联系方式</button>
            <button type="button" :class="['btn',isActive=='公开群'?'btn-success':'btn-outline-secondary']" @click="choseType('公开群')">公开群</button>
          </div>
          <div class="form-group">
            <label for="exampleInputPassword1"><b>链接网址</b></label>
            <input type="url" class="form-control" id="exampleInputPassword1" v-model="Link.url" placeholder="https://www.example.com">
            <small id="emailHelp" class="form-text text-muted">请务必填写完整链接地址.</small>
          </div>
          <div class="text-right">
            <button class="btn btn-primary" @click.prevent="onSubmit">创建</button>
            <button class="btn btn-danger" @click.prevent="resetLink">清除</button>
          </div>
        </form>

      </div>
      <div class="col-12">
        <table class="table table-responsive-sm table-responsive-md table-borderless table-hover">
          <thead>
          <tr>
            <th >名称</th>
            <th >类型</th>
            <th >网址</th>
            <th class="text-center">操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(link,index) in links">
            <td>{{ link.name }}</td>
            <td>{{ link.type }}</td>
            <td>{{ link.url }}</td>
            <td>
              <button class="mx-auto btn btn-danger nav-link" @click="deleteRow(index,link)">移除</button>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LinkManager",
  data() {
    return {
      Link: {
        name: '',
        type: '',
        url: ''
      },
      isActive:"",
      resoutmsg:"添加一条数据吧",
      links:[],
    };
  },
  created() {
    this.loadLinks()
  }
  ,
  methods: {
    choseType(val){
      this.Link.type = val;
      this.isActive = val;
    },
    onSubmit(){
      if (this.Link.name ==""||this.Link.type == "" || this.url == ""){
        alert("请补充完整数据")
        return
      }
      const query = Bmob.Query('LinkTable');
      query.set("name",this.Link.name)
      query.set("type",this.Link.type)
      query.set("url",this.Link.url)
      query.save().then(res => {
        this.resoutmsg = "添加成功，数据id: "+ res.objectId
        this.loadLinks()
        this.Link = {}
      }).catch(err => {
        this.resoutmsg = "添加失败"
      })
    },
    loadLinks(){
      const query = Bmob.Query("LinkTable");
      query.find().then(res => {
        this.links = res
      });
    },
    deleteRow(index,data){
      const query = Bmob.Query('LinkTable');
      query.destroy(data.objectId).then(res => {
        console.log(res)
        this.loadLinks()
      }).catch(err => {
        console.log(err)
        alert("删除失败")
      })
    },
    resetLink(){
      this.Link = {}
      this.isActive = -1
      this.resoutmsg="添加一条数据吧"
    }
  }
}
</script>

<style scoped>
td{
  white-space: normal;
  word-break: keep-all;
  min-width: fit-content;
}
</style>



